body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

ul{
    list-style: none
}

.navbar-btn{
    width:80px;
    margin: 0 0.2em;
}




.sidebar-btn {
    margin-bottom: 0.5em;
    width: 50%;
}
.build-btn{
    width: 100%;
    font-size: 1.5em
}


.button-group{
    margin: 0.5em 0;
}

.disable-link{
    pointer-events: none
}
.word-list {
    border: 1px solid #aaa;
    border-radius: 10px;
    height: 70vh;
    overflow: scroll;
}

.word-list ul{
    list-style: none;
    padding:5px 0;
    margin: 0;
}

.word-list ul li{
    border-radius: 10px;
}
.word-list ul li:hover{
    background: salmon;
    font-weight: 600;
}
.search-bar {
    margin-bottom: 1em;
    display: flex;
    flex-flow: row wrap;
}

form{
    flex:1 1;
    width: 100%;
}

#search-topic-input,#search-word-input{
    width: 100%;
}
.word-selector {
    position: relative;
    height: 70vh;
    background: #eee;
    border-radius: 5px;
    border: 1px black solid;
    overflow: scroll;
}

.word{
    padding: 0 0.5em;
    display:inline-block;
    min-height: 50px;
    line-height: 50px;
    min-width: 100px;
    width: 50px;
    white-space: nowrap;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background: lightgreen;
    border-radius: 10px;
    
}

.word:hover{
    z-index: 10;
    -webkit-animation: grow 1s linear both;
            animation: grow 1s linear both;
}

.chosen-word{
    min-height: 70px;
    line-height: 70px;
    min-width: 130px;
    background: salmon
}

.ml{background-color: lightblue}
.rel_syn{background-color: lightcoral}
.rel_trg{background-color: lightgoldenrodyellow}
.rel_jja{background-color: lightgrey}
.rel_jjb{background-color: lightslategray}
.rel_gen{background-color: lightgreen}
.rel_com{background-color: lightpink}
#title-input{
    font-size: 2em;
    width: 100%;
    margin: 0.5em 0;

}


